import { Category } from './../../providers/category/category.model';
import { NoticeProvider } from './../../providers/notice/notice';
import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, Content, ViewController, Events } from 'ionic-angular';
import { CategoryProvider } from '../../providers/category/category';

@IonicPage()
@Component({
  selector: 'page-feed',
  templateUrl: 'feed.html',
  providers: [  CategoryProvider ]
})
export class FeedPage {

  /**
   * Indica se esta ativo
   */
  public ativo = false;

  /**
   * Total de paginas
   * 
   */
  public totalPages = 1;

  /**
   * Pagina atual
   * 
   */
  public actualPage:any = 0;

  /**
   * Inidica se esta no request ou nao
   * 
   */
  public isOnRequest = false;
  
  /**
   * Categorias e filtros
   * 
   */
  public categories: Category[] = [];

  /**
   * Categorias no item ALL
   * 
   */
  public inAllCategories: Category[] = [];

  /**
   * Noticias sendo listadas
   * 
   */
  public notices = [];

  /**
   * Filtro selecionado
   * 
   */
  public currentFilter = null;

  /**
   * Feed pessoal
   * 
   */
  public feedPessoalName = '';

  /**
   * IonContent
   * 
   */
  @ViewChild(Content) content: Content;

  /**
   * Método construtor
   * 
   * @param navCtrl 
   * @param navParams 
   * @param modalCtrl 
   * @param notice 
   * @param categoryProvider
   */
  constructor(public navCtrl: NavController, 
              public navParams: NavParams,
              public modalCtrl: ModalController,
              public notice: NoticeProvider,
              public viewCtrl: ViewController,
              public events: Events,
              public categoryProvider: CategoryProvider ) {
      
    // Seta o nome do feed pessoal
    this.feedPessoalName = ( this.navParams.get( 'name' ) ) ?
    this.navParams.get( 'name' ) : '';
  }

  /**
   * Carrega as primeiras noticias
   * 
   */
  ionViewDidLoad() {
    
    // carrega as noticias
    this.loadNews( false, true );

    // Se inscreve no evento
    this.events.subscribe( 'update:categories', () => {
      this.categories      = this.categoryProvider.listActived();
      this.inAllCategories = this.categoryProvider.listInAll();
    });

    // Pega as categorias
    this.categories      = this.categoryProvider.listActived();    
    this.inAllCategories = this.categoryProvider.listInAll();  
  }

  /**
   * Seta a categoria ativa
   * 
   * @param id 
   */
  setActivedCategory( id ) {
    this.currentFilter = id;
    this.loadNews( false, true );
  }

  /**
   * Carrega novas noticias
   * 
   * @param event 
   * @param reset 
   */
  loadNews( event:any = false, reset = false ) {
    
    // Verifica se está e uma request
    if( this.isOnRequest ) return;
    this.isOnRequest = true;

    // Verifica se deve resetar
    if( reset ) {
      this.actualPage = 0;
      this.totalPages = 1;
    }

    // Seta as categorias de busca
    let cats: any = this.inAllCategories;
    if ( this.currentFilter ) {
      cats = [{ id: this.currentFilter } ];
    }

    // Caso nao venha nenhuma categoria
    if( cats.length == 0 ) {
      cats = this.categoryProvider.listInAll(); 
    }

    // busca as noticias
    this.notice.getNotices( parseInt(this.actualPage) + 1, cats )
    .then( ( notices: any ) => {
      
      // verifica se deve resetar
      if( !reset ) {
        for( let notice of notices.data ) this.notices.push( notice );
      } else {
        this.notices = notices.data;
        this.content.scrollToTop();
      }

      // seta as paginações
      this.totalPages = notices.total_pages;
      this.actualPage = notices.page;
    })
    .then( () => { 

      // Indica que não está mais em um request
      this.isOnRequest = false;

        // Verifica se existe um evento
        if( event ) event.complete();
      });
  }

  /**
   * Abre a pagina de pesquisa
   * 
   */
  pesquisar() {
    this.navCtrl.push( 'PesquisarPage' );
  }

  /**
   * Abre o modal de filtro
   * 
   */
  filtrarModal( is_first_time = false ) {
    const modal = this.modalCtrl.create( 'FiltrarModalPage', { is_first_time } );
    modal.present();
    modal.onDidDismiss( data => {   
      this.loadNews( false, true );
    });
  }
}

