import { Component, Input, SimpleChanges } from '@angular/core';
import { OnChanges } from '@angular/core/src/metadata/lifecycle_hooks';
import { AppConfig } from './../../app/app.config';
declare const FacebookAds: any;

@Component({
  selector: 'post-ads',
  templateUrl: 'post-ads.html'
})
export class PostAdsComponent implements OnChanges {

  /**
   * Indica se carregou a noticia
   * 
   */
  nativeAdd;

  /**
   * Scroll input
   * 
   */
  @Input() content;

  /**
   * Identificador
   * 
   */
  id;

  /**
   * Método constructor
   * 
   */
  constructor() {
    this.publishAdd();
    this.id = this.guid();
  }

  /**
   * Quando tiverem alterações
   * 
   * @param changes 
   */
  ngOnChanges(changes: SimpleChanges) {
    if (this.content) {
      this.content.ionScroll.subscribe((data) => {
        this.updateXY(data.scrollLeft, data.scrollTop);
      });
    }
  }

  /**
   * Gera um id aleatorio
   * 
   */
  guid() {
    const s4 = () => {
      return Math.floor((1 + Math.random()) * 0x10000)
        .toString(16)
        .substring(1);
    }
    return s4() + s4() + '-' + s4() + '-' + s4() + '-' + s4() + '-' + s4() + s4() + s4();
  }

  /**
   * Seta a area clicavel
   * 
   * @param top 
   */
  updateXY(left, top) {
    const d = document.getElementById( this.id );
    if ( !d ) return;
    
    const w = document.getElementById( this.id ).clientWidth;
    const h = document.getElementById( this.id ).clientHeight;
    const x = d.offsetLeft - left;
    const y = d.offsetTop - top;
    console.log( 'chamou' );

    if (FacebookAds)
      FacebookAds.setNativeAdClickArea( AppConfig.AddMobId, x, y, w, h);

  };

  /**
  * Adiciona uma Propaganda
  * 
  */
  publishAdd() {
    document.addEventListener("onAdLoaded", (data) => {
      if (this.nativeAdd) return;
      let temp: any = JSON.parse(JSON.stringify(data));
      if (temp.adType == "native") {
        this.nativeAdd = temp['adRes'];

        /* document.getElementById('adIcon').setAttribute("src", temp.adRes.icon.url);
        document.getElementById('adCover').setAttribute("src", temp.adRes.coverImage.url);
        document.getElementById('adTitle').innerHTML = temp.adRes.title;
        document.getElementById('adBody').innerHTML = temp.adRes.body;
        document.getElementById('adSocialContext').innerHTML = temp.adRes.socialContext;
        document.getElementById('adBtn').innerHTML = temp.adRes.buttonText; */
      }
    });
    if (FacebookAds) {
      FacebookAds.createNativeAd(AppConfig.AddMobId);
    }
  }
}

